@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.X{color:red}.O{color:green}input[type="text"]:focus{outline:none}


.square{background:transparent;display:flex;align-items:center;justify-content:center;cursor:pointer;outline:none;font-size:10vh}.square.X{color:#fff;font-weight:700}.square.O{color:#fff;font-weight:700}.square:not(:nth-child(3n)){border-right:2px dashed #ffffff4a}.square:not(:nth-last-child(-n+3)){border-bottom:2px dashed #ffffff4a}

.layout{display:grid;grid-template:repeat(3, 1fr)/repeat(3, 1fr);grid-gap:1px;width:444px;height:444px;border-radius:46px}

.player-card{display:flex;margin:2vh}.player-card div,.player-card span,.player-card button{margin-left:1vh}.winner-tag{text-align:center;color:#fb9e01;text-transform:uppercase;font-size:2.5vh;margin-bottom:1vw;font-weight:500}.turn-tag{text-align:center;color:#fb9e01;font-size:2.5vh;margin-bottom:1vw;font-weight:500}.draw-tag{text-align:center;color:#fb9e01;text-transform:uppercase;font-size:2.5vh;margin-bottom:1vw;font-weight:500}.player-name{width:123px;height:137px;background:#253952 0% 0% no-repeat padding-box;border-radius:24px;opacity:1;margin:0 5vw;text-align:center}.player-name.winner{border:#fb9e01 2px solid}.player-name .header{font-size:1vh;letter-spacing:0px;color:#fb9e01;opacity:1;padding-top:2vh}.player-name .name{font-size:2vh;letter-spacing:0px;color:#ffffff;padding-top:1vh}.player-name .img-holder{color:#fff;font-size:5vh;font-weight:700}.btn-holder{display:flex;justify-content:center}.btn-holder .btn-nxt{background:transparent linear-gradient(270deg, #fba202 0%, #ff5800 100%) 0% 0% no-repeat padding-box;box-shadow:0px 3px 6px #000000;border-radius:2px;opacity:1;width:80%;height:5vh;border:none;margin-top:3vh;color:#fff}.dot-container{display:flex;justify-content:center;margin-top:1vh}.dot-container .dot{height:1vh;width:1vh;background-color:#253952;border-radius:50%;display:inline-block;margin:0 0.5vh}.dot-container .dot.active{background-color:#fff}.text-holder{height:4vh}

.winner-page{display:flex;flex-direction:column;align-items:center}.winner-page .winner-header{text-transform:uppercase;color:#fff;font-size:4vh}.winner-page .winner-name{width:230px;height:256px;background:#253952 0% 0% no-repeat padding-box;border-radius:24px;opacity:1;margin:6vh 6vh;text-align:center;border:#fb9e01 2px solid}.winner-page .winner-name .header{font-size:2vh;letter-spacing:0px;color:#fb9e01;opacity:1;padding-top:2vh;text-transform:uppercase}.winner-page .winner-name .name{font-size:4vh;letter-spacing:0px;color:#ffffff;padding-top:1vh}.winner-page .winner-name .img-holder{color:#fff;font-size:10vh;font-weight:700}

.login-layout{height:100vh;background:#0b233f 0% 0% no-repeat padding-box;opacity:1;justify-content:center;display:flex;align-items:center}.login-layout .logo{position:absolute;top:2vh;left:4vw}.login-layout .login-holder{width:480px;height:480px;border:2px dashed #ffffff4a;border-radius:46px}.login-layout .login-holder .login-card{background:#253952 0% 0% no-repeat padding-box;box-shadow:0px 3px 6px #000000dd;border-radius:46px;width:444px;height:444px;margin:2vh}.login-layout .login-holder .login-card .login-header{display:flex;justify-content:center;padding-top:5vh}.login-layout .login-holder .login-card .login-header .highlight{text-align:left;font:normal normal 600 23px/25px Poppins;letter-spacing:0.83px;color:#fba202}.login-layout .login-holder .login-card .login-header .header-text{text-align:left;font:normal normal normal 23px/25px Poppins;letter-spacing:0.83px;color:#f0f0f0;margin-right:1vh}.login-layout .login-holder .login-card .player-content{display:flex;justify-content:center;align-items:center;flex-direction:column;padding:2vh}.login-layout .login-holder .login-card .player-content .formgroup{width:80%;margin-top:3vh}.login-layout .login-holder .login-card .player-content .formgroup .label{font:normal normal normal 12px/16px Roboto;letter-spacing:0px;color:#ffffff;margin-bottom:1vh}.login-layout .login-holder .login-card .player-content .formgroup .input-p{border:1px solid #f0f0f0;border-radius:2px;width:100%;height:5vh;background:transparent;color:#fff}.login-layout .login-holder .login-card .btn-sub{background:transparent linear-gradient(270deg, #fba202 0%, #ff5800 100%) 0% 0% no-repeat padding-box;box-shadow:0px 3px 6px #000000;border-radius:2px;opacity:1;width:80%;height:5vh;border:none;margin-top:3vh;color:#fff}

